import React from "react";
import PropTypes from "prop-types";
import { graphql } from "gatsby";
import { CopyToClipboard } from "react-copy-to-clipboard";
import {
  SiteHead,
  Cheatsheet,
  GitEmoji,
  GitEmojiItem,
  Emoji
} from "../components";

const CommitCheatsheetPage = ({
  data: {
    allContentJson: {
      edges: [
        {
          node: { gitemoji, convention }
        }
      ]
    }
  }
}) => (
  <Cheatsheet>
    <SiteHead fixedTitle="Commit Cheatsheet" />
    <GitEmoji>
      {gitemoji.items.map(({ emoji, code, description, name }) => (
        <CopyToClipboard
          key={emoji}
          text={code}
          onCopy={() => console.log(`Copied ${code}`)}
        >
          <GitEmojiItem>
            <Emoji className={name}>
              <span>{emoji}</span>
            </Emoji>
            <div css="padding: 0.5rem;">
              <div>{code}</div>
              <div>{description}</div>
            </div>
          </GitEmojiItem>
        </CopyToClipboard>
      ))}
    </GitEmoji>
    <ul>
      {convention.items.map(({ title, description }) => (
        <li key={title}>
          <CopyToClipboard
            text={title}
            onCopy={() => console.log(`Copied ${title}`)}
          >
            <strong css="cursor: pointer;">{title}</strong>
          </CopyToClipboard>
          : {description}
        </li>
      ))}
    </ul>
  </Cheatsheet>
);

CommitCheatsheetPage.propTypes = {
  data: PropTypes.shape({
    allContentJson: PropTypes.shape({
      edges: PropTypes.array
    })
  }).isRequired
};

export default CommitCheatsheetPage;

export const query = graphql`
  query {
    allContentJson {
      edges {
        node {
          gitemoji {
            items {
              name
              emoji
              code
              description
            }
          }
          convention {
            items {
              title
              description
            }
          }
        }
      }
    }
  }
`;
